import { useState, createContext } from 'react';
import { singleProjectData as contexteBasique } from '../data/singleProjectData';
import { singleProjectData as contexteDomotique } from '../data/singleProjectDataDomotique';
import { singleProjectData as contexteAquarium } from '../data/singleProjectDataAquarium';

const SingleProjectContext = createContext();
let context;
export const SingleProjectProvider = ({ children ,...props}) => {

	switch (props.context) {
		case "domotique":
			context = contexteDomotique;
			break;		
			
			case "aquarium":
			context = contexteAquarium;
			break;
	
		default:
			context = contexteBasique
			break;
	}

	const [singleProjectData, setSingleProjectData] = useState(
		context
	);

	return (
		<SingleProjectContext.Provider
			value={{ singleProjectData, setSingleProjectData }}
		>
			{children}
		</SingleProjectContext.Provider>
	);
};

export default SingleProjectContext;
